import axios from '../axios'

/* 
 * 
 */

// 
export const save = (data) => {
    return axios({
        url: '/pmsProductCategory/insertPmsProductCategory',
        method: 'post',
        data
    })
}

//
export const deletes = (data) => {
    return axios({
        url: '/pmsProductCategory/deletePmsProductCategory',
        method: 'post',
        data
    })
}


